/* Footer style */

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  min-height: 60px;
  background-color: #6ED3CF;
}
.footer-text {
  color: $footer-primary-color;
}
.footer-link {
  color: $footer-link-color;
  text-decoration: none;
  margin-left: 10px;
}
.footer-link:hover {
  color: $footer-link-color;
  text-decoration: underline;
}