//Top navigation
.navbar.navbar-default.navbar-fixed-top{
  background: #ffffff;
  border-bottom: 1px solid #EFF1F7;
}
.activex {
  background: #EFF1F7;
  color: #21479C;
}
.nav.navbar-nav.navbar-right a {
  color:$nav-color;
  font-weight: 500;
}
.nav.navbar-nav.navbar-right a:hover {
  text-decoration: underline;
  color:$nav-color;
}
//Navigation dropdown pills
.navbar-default .navbar-toggle .icon-bar {
  background-color: $dropdown-toggle-color;;
}
.navbar-default .navbar-toggle {
  border-color: $dropdown-border-color;
}
.navbar-default .navbar-toggle:hover,
.navbar-default .navbar-toggle:focus {
  background-color: $dropdown-backgroundcolor-hover;
}

//Login btn class
.btn-login {
  background-color:$background-btn-color;
  border:1px solid $background-btn-border-color;
}
.btn-login:hover {
  background-color:$background-btn-color-hover;
  border:1px solid $background-btn-border-color;
}